(function () {
    'use strict';
    var angular = require('angular');
    require('./technicalError.component.scss');

    var technicalError = angular.module('app').component('technicalError', {
        template: require('./technicalError.component.html'),
        controller: ['$anchorScroll', 'analyticService', 'appState', function ($anchorScroll, analyticService, appState) {
            var vm = this;
            vm.$onInit = function () {
                appState.clearAll();
                $anchorScroll();
            };
            vm.exit = function () {
                analyticService.buttonList.buttonClick.event_content = "exit";
                analyticService.trackUserAction(analyticService.buttonList.buttonClick);

                window.location.href = "http://www.hsbc.co.in/";
            }
        }]
    });

    module.exports = technicalError.name;
})();



